@tailwind base;
@tailwind components;
@tailwind utilities;


/* 1. Hides the Google Translate banner that appears at the top */
.goog-te-banner-frame.skiptranslate {
    display: none !important;
} 

/* 2. Prevents the page content from being pushed down by the banner */
body { 
    top: 0px !important; 
}

/* 3. Removes the "Powered by" text and logo */
.goog-logo-link {
    display: none !important;
}
.goog-te-gadget {
    font-size: 0px !important; /* Hides the text "Powered by" */
    color: transparent !important;
}

/* 4. Styles the widget to look like a native part of your header */
.goog-te-gadget-simple {
  background-color: transparent !important;
  border: none !important;
  cursor: pointer;
}

/* 5. Styles the text ("Select Language") to match your site */
.goog-te-gadget-simple .goog-te-menu-value span {
  color: #374151; /* Corresponds to Tailwind's text-gray-700 */
  font-size: 0.875rem !important; /* text-sm */
  text-decoration: none !important;
}

/* 6. Style for dark mode */
.dark .goog-te-gadget-simple .goog-te-menu-value span {
    color: #D1D5DB !important; /* Corresponds to Tailwind's dark:text-gray-300 */
}